import { html } from 'lit';
export const fieldObject = (schema, data, path, uiState, uiSchema, dig, schemaPath, widgets, level = 0) => {
    const error = 'Wrong object field';
    if (typeof schema.properties !== 'object')
        return widgets.callout?.({ id: '', message: error }) ?? html `${error}`;
    const children = Object.entries(schema.properties).map(([propName, propValue]) => {
        if (Array.isArray(propValue) || typeof propValue === 'boolean')
            return html ``;
        const value = data?.[propName];
        const subPath = [...path, propName];
        const required = schema.required?.includes(propName);
        const schemaPathAugmented = [...schemaPath];
        schemaPathAugmented.push(propName);
        return dig(propValue, value, subPath, uiState?.[propName], uiSchema?.[propName], schemaPathAugmented, required, level + 1);
    });
    let label;
    const key = path.at(-1);
    if (schema.title) {
        label = schema.title;
    }
    else if (typeof key !== 'number') {
        label = key;
    }
    if (typeof uiSchema?.['ui:title'] === 'string')
        label = uiSchema['ui:title'];
    const options = {
        id: path.join('.'),
        label,
        helpText: schema.description,
        children,
        level,
    };
    return (widgets?.object?.(options) ??
        widgets?.callout?.({ id: '', message: error }) ??
        html `${error}`);
};
