import { html } from 'lit';
import { ifDefined } from 'lit/directives/if-defined.js';
import '@material/web/textfield/outlined-text-field.js';
export const textarea = (options) => html `
	<md-outlined-text-field
		.type=${'textarea'}
		.supportingText=${options.helpText}
		.id=${options.id}
		.label=${options.label}
		maxLength=${ifDefined(options.maxLength)}
		minLength=${ifDefined(options.minLength)}
		.name=${options.id}
		pattern=${ifDefined(options.pattern)}
		.placeholder=${options.placeholder}
		.required=${options.required}
		value=${ifDefined(options.value)}
		@input=${(event) => {
    const { value: newValue } = event.target;
    options.valueChangedCallback?.(newValue);
}}
		rows=${4}
	>
	</md-outlined-text-field>
`;
